.sliderContainer {
  height: 30vh;
  width: 99%;

  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.slides {
  display: flex;
  justify-content: center;
  align-items: center;
}

.inactive {
  display: none;
}

h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

p {
  font-size: 1rem;
  font-weight: 300;
}

.allDots {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.dot {
  cursor: pointer;
  height: 1rem;
  width: 1rem;
  margin: 0 3px;
  background: var(--cinza);
  border-radius: 10%;
  display: inline-block;
  margin: 4px;
  transition-duration: 100ms;
  transition-timing-function: ease-in;
}

.activeDot {
  display: inline-block;
  cursor: pointer;
  background: var(--azul);
  transition-duration: 300ms;
}

@media (max-width: 650px) {
  .sliderContainer {
    display: flex;
  }
}
