.containerImage {
  display: flex;
  height: 100vh;

  align-items: center;

  background-image: url("../../pages/firstPage/images/background_1.png");
  background-position: center center;
  background-attachment: local;
  background-repeat: no-repeat;
  background-size: cover;
}

.laptop {
  width: 58vw;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.laptop img {
  width: 90%;
}

.info {
  width: 42vw;
  height: 100%;
  display: flex;

  flex-direction: column;
  text-align: center;
  justify-content: center;
}
.info h2 {
  font-size: 1.6rem;
  font-weight: bolder;
}

.info p {
  margin-top: 1rem;

  text-align: justify;
  color: var(--cinza);
}

.info button {
  background: #fff;
  color: var(--azul);
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;

  border: none;
  border-radius: 50px;
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.192);

  margin-top: 2rem;
  align-self: center;

  width: 10.5rem;
  height: 3.5rem;

  cursor: pointer;
}

#responsive {
  display: none;
}

@media screen and (max-width: 650px) {
  .containerImage {
    flex-direction: column;
  }

  #responsive {
    display: flex;
    text-align: center;
    margin-bottom: 10vh;
    margin-top: 10vh;
    font-size: 2rem;
    font-weight: bold;
  }
  .laptop {
    height: 60vh;
    width: 100%;
  }

  .info {
    justify-content: start;
    height: 40vh;
    width: 100%;
  }

  .info h2 {
    display: none;
  }

  .info p {
    display: none;
  }
  .info button {
    background: var(--azul);
    box-shadow: 0 0 0 0 0;
    color: #fff;

    border-radius: 200px;
    font-size: 2rem;

    margin-top: 5rem;
    width: 60%;
    height: 35%;
  }
}
